@keyframes stretch {
    0% {
        transform: scale(.7);
        opacity: 1;
    }

    100% {
        transform: scale(1.0);
        opacity: 0.7;
    }
}

.hotspot {
    animation-name: stretch;
    animation-duration: 1.0s;
    animation-timing-function: ease-out;
    animation-direction: alternate;
    animation-iteration-count: infinite;
    animation-play-state: running;
}
